import Calendar from "react-calendar";
import dayjs from "dayjs";
import { useEffect, useState } from "react";
import MenuForm from "../../components/menu1/MenuForm";
import MenuList from "../../components/menu1/MenuList";
import { initList } from "../../temp-data/menu1-data";
import Temp from "../../components/menu1/Temp";

function Menu1() {
  const [ymd, setYmd] = useState(dayjs().format("YYYY-MM-DD")); //  
  const [selectedKey, setSelectedKey] = useState(0);
  const [list, setList] = useState([]); // 

  // init
  useEffect(() => {
    // temp data
    const temp = initList();
    temp.map((v) => saveData(v));
  }, []);

  // 
  const formAttr = {
    // str: "",
    radio: [
      { value: "Y", title: "" },
      { value: "N", title: "" },
    ],
    checkbox: [
      { value: "cat", title: "" },
      { value: "dog", title: "" },
      { value: "rabbit", title: "" },
    ],
  };

  const saveData = (params = {}) => {
    setList((prev) => {
      const keyList = [...prev].map((v) => v.key);
      const maxKey = keyList.length > 0 ? Math.max(...keyList) : 0;
      // console.log({ keyList, maxKey });
      return [...prev, { ...params, key: maxKey + 1, ymd: params.ymd || ymd }];
    });
  };

  const removeData = (key) => {
    setList((prev) => [...prev.filter((v) => v.key !== key)]);
  };

  return (
    <>
      <div className="container mt-2">
        <div className="row mb-4">
          {/*  */}
          <div className="col-6 col-md-4">
            <Calendar
              value={ymd}
              onChange={(value) => {
                setYmd(dayjs(value).format("YYYY-MM-DD"));
                setSelectedKey(0);
              }}
              locale="ko"
            />
          </div>

          {/* input form */}
          <div className="col-md-8">
            <div className="card">
              <div className="card-body">
                <div className="card-title">
                  <span className=" h5 pe-2">: {ymd}</span>
                  <span className="card-subtitle mb-2 form-text">
                       .
                  </span>
                </div>

                <MenuForm
                  formAttr={formAttr}
                  list={list}
                  selectedKey={selectedKey}
                  saveData={saveData}
                  removeData={removeData}
                />
              </div>
            </div>
          </div>
        </div>

        {/* row */}
        <Temp date={ymd} list={list} />

        {/*  */}
        <MenuList
          formAttr={formAttr}
          list={list}
          setYmd={setYmd}
          selectedKey={selectedKey}
          setSelectedKey={setSelectedKey}
        />
      </div>
    </>
  );
}

export default Menu1;
